@font-face {
  font-family: "Ubuntu";
  font-weight: 700;
  font-display: swap;
  src: url(../fonts/Ubuntu/Ubuntu-Bold.woff);
}
@font-face {
  font-family: "Ubuntu";
  font-weight: 400;
  font-display: swap;
  src: url(../fonts/Ubuntu/Ubuntu-Regular.woff);
}
@font-face {
  font-family: "Ubuntu";
  font-weight: 400;
  font-style: italic;
  font-display: swap;
  src: url(../fonts/Ubuntu/Ubuntu-Italic.woff);
}
.container {
  max-width: 1200px;
  padding: 0 1rem;
  margin: 3rem auto;
}

