*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  color: hsl(0deg, 0%, 0%);
  font-family: "Ubuntu", sans-serif;
  background-color: hsl(0deg, 0%, 100%);
}

a {
  color: hsl(0deg, 0%, 0%);
  font-style: italic;
  outline: none;
  text-underline-offset: 3px;
  text-decoration-thickness: 2px;
  transition: text-decoration 0.2s ease-out;
}
a:hover, a:focus, a:focus-within {
  text-decoration-color: hsl(180deg, 100%, 40%);
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.badge {
  position: absolute;
  top: -10px;
  right: -10px;
  padding: 0.5rem;
  border: 2px solid hsl(0deg, 0%, 0%);
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.2);
}
.badge.badge--green {
  background-color: hsl(97deg, 100%, 79%);
}

.solution {
  display: grid;
  gap: 1.5rem;
  padding: 0 1rem;
  margin-top: 4rem;
}
@media screen and (min-width: 31.25rem) {
  .solution {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media screen and (min-width: 53.13rem) {
  .solution {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media screen and (min-width: 68.75rem) {
  .solution {
    grid-template-columns: repeat(4, 1fr);
  }
}
.solution__item {
  display: flex;
  position: relative;
  flex-direction: column;
  transition: transform 0.25s ease-out;
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.2);
}
.solution__item.active, .solution__item:hover {
  transform: translateY(-10px);
}
.solution__item.active .solution__link {
  text-decoration-color: hsl(180deg, 100%, 40%);
}
.solution__img-container {
  display: inline-block;
  font-style: normal;
  text-decoration: none;
  border: 2px solid hsl(0deg, 0%, 0%);
}
.solution__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center top;
}
.solution__content {
  font-size: 1.2rem;
  padding: 0.8rem;
  border: 2px solid hsl(0deg, 0%, 0%);
  border-top: none;
  background-color: hsl(34deg, 78%, 91%);
}

@media (prefers-reduced-motion: reduce) {
  *,
::before,
::after {
    animation-delay: -1ms !important;
    animation-duration: 1ms !important;
    animation-iteration-count: 1 !important;
    background-attachment: initial !important;
    scroll-behavior: auto !important;
    transition-duration: 0s !important;
    transition-delay: 0s !important;
  }
}